.table-box{
    margin: 1rem auto;
    margin-bottom: 3rem;
}
.bmi-score{
    border-radius: 1rem;
    background-color: #181818;
    margin: 1rem auto;
    padding: 1rem;
    text-align: center;
    width: 20rem;
    margin-top: 2rem;
}
.bmi-value{
    padding: .5rem 1.2rem;
    background-color: #333;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: .7rem auto;
    border-radius: .5rem;
    font-size: 2.5rem;
    font-weight: 600;
}
.bmi-type{
    padding: .3rem .5rem;
    font-weight: 600;
    background-color: #222;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border-radius: .4rem;
    margin: .1rem auto .5rem;
}
.forms{
    border-radius: 1rem;
    background-color: #181818;
    margin-left: auto;
    margin-right: auto;
    padding: 1rem;
    text-align: center;
}
.formdiv{
    margin-top: 3rem;
}
.inp{
    background-color: #333;
    border-color: gray;
    color: #fff;
}
.inp:hover, .inp:focus{
    background-color: #222;
    box-shadow: none;
    border-color: gray;
    color: #fff;
}
.button{
    background-color: #252525;
    color: #fff;
}
.button:hover{
    color: gray;
}
body{
    background-color: #000;
    color: #fff;
    margin:0;
}
.bmi-data{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
